body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
}

.Nav_menu {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  width: 100%;
  height: 100%;
  list-style-type: none;
  margin: 0;
  padding: 0;
}
.Nav_menu ul {
  margin: 0 auto;
  padding: 0;
}

.Nav_link:link {
  color: black;
  font-size: 1.2em;
  text-decoration: none;
  padding-right: 10px;
}
.Nav_link:hover {
  color: yellow;
  
}
.Nav_link:active {
  color: teal;
}

.activeRoute {
  color: red;
}

